<template>
  <div>
    <Banner></Banner>
  </div>
</template>

<script>
import Banner from './Banner'
import TopCourse from './TopCourse'
// axios
import {getBannerData} from '@/network/home.js'
export default {
  name: "Home",
  components: {
    Banner,
    TopCourse
  },
  data() {
    return {
      imgs: []
    }
  },
  mounted() {
    getBannerData().then(result => {
      this.imgs = result.data
        alert(this.imgs)
    })
  }
};
</script>

<style lang="css" scoped>
  img {
    width: 100%;
  }
</style>



